<template>
  <div id="app">
    <div id="nav">
      <span v-if="userRole === 'admin'">
        <router-link to="/register">Register</router-link>|
        <router-link to="/faсtoryAdd">FaсtoryAdd</router-link>|
        <router-link to="/userList">userList</router-link>
      </span>
      <span v-if="userRole === 'worker'">
        <router-link v-if="userRole === 'worker'" to="/workTimeInput">WorkTime Input</router-link>|
        <router-link v-if="userRole === 'worker'" to="/about">about</router-link>
      </span>
      <router-link v-if="!isLoggedIn" to="/login">Login</router-link>
      <span v-else>
        <a @click="logout"> | Logout</a>
      </span>
    </div>
    <router-view/>
  </div>
</template>
<script>
export default {
  computed: {
    isLoggedIn: function() {
      return this.$store.getters.isLoggedIn;
    },
    userRole: function() {
      return this.$store.getters.userRole;
    }
  },
  methods: {
    logout: function() {
      this.$store.dispatch("logout").then(() => {
        this.$router.push("/login");
      });
    }
  },
  created: function() {
    this.$http.interceptors.response.use(undefined, function(err) {
      return new Promise(function(resolve, reject) {
        if (err.status === 401 && err.config && !err.config.__isRetryRequest) {
          this.$store.dispatch(logout);
        }
        throw err;
      });
    });
  }
};
</script>

