<template>
  <div>
    <div v-if="date">
      <form @submit.prevent="commit">
        <label for="date" @click="earseData">{{date}}</label>
        <div>
          <VueCtkDateTimePicker
            id="start_time"
            minuteInterval="15"
            label
            formatted="HH:mm"
            format="HH:mm"
            onlyTime
            noLabel
            v-model="start_time"
          />
        </div>

        <label for="end_time">end_time</label>
        <div>
          <VueCtkDateTimePicker
            id="start_time"
            minuteInterval="15"
            label
            formatted="HH:mm"
            format="HH:mm"
            onlyTime
            noLabel
            right
            v-model="end_time"
          />
        </div>

        <label for="lunch">lunch</label>
        <div>
          <input id="lunch" type="text" v-model="lunch">
        </div>
        <div>
          <input id="description" type="text" v-model="description">
        </div>
        <div>
          <button type="submit">Add</button>
        </div>
      </form>
    </div>
    <div v-else>
      <VueCtkDateTimePicker
        id="date"
        format="YYYY-MM-DD"
        max-date="2019-12-01"
        min-date="2019-09-01"
        first-day-of-week= 1
        label
        onlyDate
        inline
        overlay
        right
        v-model="date"
      />
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      date: null,
      start_time: "16:20",
      end_time: "18:30",
      lunch: "req.body.lunch",
      description: "req.body.description",
      factory: ["null"]
    };
  },
  mounted() {
    axios({ url: "api/factory", method: "GET" }).then(
      response => (this.factory = response.data[0]._id)
    );
  },
  methods: {
    earseData: function() {
      this.date = null;
    },
    commit: function() {
      let data = {
        start_time: `${this.date}T${this.start_time}:00.000Z`,
        end_time: `${this.date}T${this.end_time}:00.000Z`,
        lunch: this.lunch,
        description: this.description,
        factory: this.factory
      };
      axios({ url: "api/userWorkTime", data, method: "POST" }).then();
      //   this.$store
      //     .dispatch("commit", data)
      //     .then(() => this.$router.push("/"))
      //     .catch(err => console.log(err));
    }
  }
};
</script>
