<template>
  <div>
    <h4>Register</h4>
    <form @submit.prevent="register">
      <label for="first_name">first_name</label>
      <div>
        <input id="first_name" type="text" v-model="first_name" required autofocus>
      </div>

      <label for="last_name">last_name</label>
      <div>
        <input id="last_name" type="text" v-model="last_name" required>
      </div>

      <label for="date_of_birth">date_of_birth</label>
      <div>
        <input id="date_of_birth" type="text" v-model="date_of_birth" required>
      </div>

      <label for="email">E-Mail Address</label>
      <div>
        <input id="email" type="email" v-model="email" required>
      </div>

      <label for="user_name">user_name</label>
      <div>
        <input id="user_name" type="text" v-model="user_name" required>
      </div>

      <label for="password">Password</label>
      <div>
        <input id="password" type="password" v-model="password" required>
      </div>

      <div>
        <button type="submit">Register</button>
      </div>
    </form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      first_name: "",
      last_name: "",
      date_of_birth: "YYYY-MM-DD",
      email: "",
      user_name: "",
      password: "",
    };
  },
  methods: {
    register: function() {
      let data = {
        first_name: this.first_name,
        last_name: this.last_name,
        date_of_birth: this.date_of_birth,
        email: this.email,
        user_name: this.user_name,
        password: this.password
      };
      this.$store
        .dispatch("register", data)
        .then(() => this.$router.push("/"))
        .catch(err => console.log(err));
    }
  }
};
</script>
