<script>
import axios from "axios";
export default {
  data() {
    return {
      users: []
    };
  },
  mounted() {
    axios({ url: "api/user", method: "GET" }).then(
      response => (this.users = response.data)
    );
  },
  methods: {
    select: function(index) {
      axios.get(`api/user/${this.users[index]._id}`).then(res => {
        alert(res);
      });
    }
  }
};
</script>

