import Vue from 'vue'
import Router from 'vue-router'
import store from './store.js'
import Home from './views/Home.vue'
import About from './views/About.vue'
import Login from './components/Login.vue'
import Secure from './components/Secure.vue'
import Register from './components/Register.vue'
import WorkTimeInput from './components/WorkTimeInput.vue'
import FaсtoryAdd from './components/FaсtoryAdd.vue'
import UserList from './components/UserList.vue'
import UserFactory from './components/UserFactory.vue'

Vue.use(Router)

let router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/userFactory',
      name: 'UserFactory',
      component: UserFactory
    },
    {
      path: '/workTimeInput',
      name: 'WorkTimeInput',
      component: WorkTimeInput
    },
    {
      path: '/userList',
      name: 'UserList',
      component: UserList
    },
    {
      path: '/faсtoryAdd',
      name: 'FaсtoryAdd',
      component: FaсtoryAdd,
      meta: { 
        requiresAdmin: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta: { 
        requiresAdmin: true
      }
    },
    {
      path: '/secure',
      name: 'secure',
      component: Secure,
      meta: { 
        requiresAuth: true
      }
    },
    {
      path: '/about',
      name: 'about',
      component: About
    }
  ]
})

router.beforeEach((to, from, next) => {
  if(to.matched.some(record => record.meta.requiresAuth)) {
    if (store.getters.isLoggedIn) {
      next()
      return
    }
    next('/login')
  } else if (to.matched.some(record => record.meta.requiresAdmin)) {
    if (store.getters.userRole == 'admin') {
      next()
      return
    }
    next('/')
  } else {
    next() 
  }
})

export default router